.container{
  display: flex;
  width: 100%;
  padding: 1rem 0 0;
  border-bottom: 1px solid #eeeeee;

  .content{
    width: 70%;
    padding-right: .5rem;
  }

  .fixedInfo{
    width: 30%;
    position: sticky;
    right: 0;
    top: 63px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    .picProfile{
      width: 12.5rem;
      border-radius: 50%;
    }
  }
}

.emoji{
  font-size: 14px;
}

.additionalInfo{
  margin: 2rem 0 1rem;
  width: 90%;

  .data{
    padding: 1.5rem 0 .5rem;

    .titleData{
      font-weight: 600;
      text-transform: uppercase;
      font-size: 22px;
      padding-right: 1.5rem;
      padding-bottom: .5rem;
      background-repeat: no-repeat !important;
      background-position: bottom right !important;
      width: fit-content;
    }

    .descData{
      font-size: 18px;
      padding-left: 1rem;
    }
  }
}

.skills{
  margin-bottom: 1rem;
  
  .skill{
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
  
    .lang{
      font-weight: 700;
      margin-right: 10px;
      min-width: fit-content;
      width: fit-content;
    }

    .barPercent{
      position: relative;
      width: 100%;
      height: 38px;
      max-height: 38px;

      .barNoComplete{
        position: absolute;
        top: 50%;
        width: 100%;
        height: 2px;
        background-color: #CECECE;
        z-index: 10;
      }

      .bar{
        position: absolute;
        top: 50%;
        transition: width 1.5s;
        transition-timing-function: ease-in;
        height: 2px;
        background-color: #161616;
        z-index: 11;
      }

      .percent{
        position: absolute;
        top: 20%;
        transition: left 1.5s;
        transition-timing-function: ease-in;
        z-index: 12;
        width: 22px;
        height: 22px;
        font-weight: 600;
        background-color: #FFF;
        border: 2px solid #161616;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 8px;
        border-radius: 50%;
        color: #000;
      }
    }
  }

  .addition{
    width: 100%;
    font-size: 13px;
  }
}

.title{
  font-weight: 700;
}

.port {
  display: inline-block;
  position: relative;
  padding-left: 2rem;
  margin: 3rem 0 1rem;

  .portContent{
    position: relative;
    z-index: 10;
    margin-top: 15px;

    .contentResponsive{
      height: 350px;
      position: relative;
      width: 100%;
      cursor: pointer;

      img{
        position: absolute;

        &.imgDesktop{
          width: 85%;
          left: 1rem;
          top: 1rem;
          padding: .7rem .7rem 1.5rem;
          background-color: #dcdcdc;
          border-radius: 15px;
          height: 15em;
        }

        &.imgMobile{
          right: 3rem;
          bottom: .5rem;
          width: 25%;
          height: auto;
          padding: 1.5rem .5rem 2.5rem;
          background-color: #212121;
          border-radius: 15px;
        }
      }

      &.onlyOne {
        display: flex;
        align-items: center;
        justify-content: center;

        img {
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          position: relative;

          &.imgMobile{
            width: 30%;
          }
        }
      }
    }

    .nameWork{
      font-size: 18px;
      font-weight: 600;
      color: #161616;

      &:hover {
        color: #25425b;
        text-decoration: none;
      }
    }
  }
}

.footer{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem 0 .5rem;
}

@media(max-width: 650px){
  .container{
    flex-direction: column-reverse;

    .content{
      width: 100%;
      padding-right: 0;
    }
  
    .fixedInfo{
      margin-bottom: 1rem;
      top: 0;
      width: 100%;
      position: relative;
      margin-bottom: 2rem;
    }
  }
  .port {
    padding-left: 0;

    .portContent{
      padding: 0 1.5rem;
    }
  }
}
